.section-3 {
    margin-top: 53px;
    box-shadow: 20px 20px 40px #000;
    height: 100vh;
    max-height: 675px;
    min-height: 580px;
    background: #191919;
    display: flex;
    justify-content: center;
    align-items: center;
    h2 {
        font-size: 80px;
        line-height: 98px;
        color: #828282;
    }
    @media screen and (max-width: 1199px) {
        h2 {
            font-size: 70px;
            line-height: 88px;
        }
        
    }
    @media screen and (max-width: 767px) {
        h2 {
            font-size: 60px;
        line-height: 78px;
        }
        
    }
    @media screen and (max-width: 575px) {
        h2 {
            font-size: 50px;
        line-height: 68px;
        }
        
    }
}