.section-2 {
    background: #0F0F0F url(../img/section-2-bg.jpg) no-repeat center center/cover;
    padding-top: 32px;
    min-height: 870px;
    position: relative;
    .container {
        height: 100%;
        min-height: 870px;
        position: relative;
    }
    h2 {
        font-size: 33px;
        line-height: 40px;
        font-weight: bold;
        text-align: center;
    }
    .all-app {
        position: absolute;
        top: calc(50% - 150.5px);
        left: calc(50% - 132.5px);

    }
    .all-app__text1 {
        position: absolute;
        font-size: 25px;
        line-height: 30px;
        font-weight: bold;
        text-align: center;
        left: 0px;
        top: 189px;
        opacity: 0;
        transition: all 1.5s ease;
    }
    .all-app__text2 {
        position: absolute;
        font-size: 25px;
        line-height: 30px;
        font-weight: bold;
        text-align: center;
        right: 5px;
        bottom: 208px;
        opacity: 0;
        transition: all 1.5s ease;
    }
    &.open .all-app__text2, &.open .all-app__text1 {
        
        opacity: 1;
    }



    .all-app_img {
        position: absolute;
        top: calc(50% - 96px);
        left: calc(50% - 55px);
        transition: all 1.5s ease;
        transition-delay: 0.5s;
        opacity: 0;
        div {
            font-size: 15px;
            line-height: 18px;
            font-weight: bold;
            text-align: center;
            margin-bottom: -2px;
        }
    }
    &.open .all-app_img-1 {
        top: 155px;
        left: 219px;
        opacity: 1;
    }
    &.open .all-app_img-2 {
        top: 136px;
