.section-5 {
    background: #0F0F0F  url(../img/section-5-bg.jpg) no-repeat center center/cover;
    padding: 90px 0 70px 0;
    box-shadow: 20px 20px 40px #000;
    // filter: brightness(0.6);
    .block {
        font-size: 32px;
        line-height: 39px;
        font-weight: bold;
        opacity: 0;
        p {
            margin-bottom: 0;
            position: relative;
            padding-right: 20px;
            &:after {
                content: url(../img/Path.png);
                display: block;
                position: absolute;
                bottom: -35px;
            }
            &:last-child {
                &:after {
                    display: none;
                }
            }
        }
      
    }

    .spera-mob {
        height: 100%;
        width: 100%;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0;
        
        img {
            zoom: 123%;
            position: absolute;
            transform: translate(38px,-14px);
        }
    }
    &.open  {
        // filter: brightness(1);
        // transition: all 1.5s ease;
        // filter: brightness(3);
        position: relative;
        z-index: 1;
       &:after {
           content: '';
           display: block;
           background: #fff;
           width: 100%;
           height: 100%;
           position: absolute;
           top: 0;
           left: 0;
           opacity: 0;
           animation-name: section-5;
           animation-duration: 1s;
           z-index: -1;
       }
        
    }
    @keyframes section-5 {
        0% {
            opacity: 0;
            z-index: 1;
        }
      
         50% {
            opacity: 0.5;
         }
        100% {
            opacity: 0;
            z-index: 1;
        }
      }
    &.open .block, &.open .spera-mob {
        opacity: 1;
        
        
        transition: all 1s ease;
        
    }

    @media screen and (max-width: 1199px) {
        .spera-mob img {
            transform: translate(0,-14px);
        }
    }
    @media screen and (max-width: 991px) {
        .block {
            font-size: 25px;
            line-height: 30px;
            p {
                &:after {
                    bottom: -30px;
                }
            }
        }
        .spera-mob img {
            zoom: 100%;
            transform: translate(0,-14px);
        }
    }
    @media screen and (max-width: 575px) {
        padding: 50px 0;
        .block {
            font-size: 20px;
            line-height: 24px;
            font-weight: normal;
            p {
                padding-right: 0;
                &:after {
                    display: none;
                }
            }
        }
    }
    @media screen and (max-width: 360px) {
        padding: 50px 0;
        .block {
            font-size: 18px;
            line-height: 22px;
        }
    }
}