.section-9 {
    background: #0F0F0F url(../img/section-9-bg.jpg) no-repeat center center/cover;
    padding: 270px 0;
    position: relative;
    margin-top: 50px;
    overflow: hidden;
    h2 {
        font-size: 50px;
        line-height: 61px;
        font-weight: bold;
        text-align: center;
    }
    &__btn {
        display: block;
        font-size: 25px;
        line-height: 70px;
        font-weight: bold;
        height: 70px;
        width: 295px;
        text-align: center;
        position: relative;
        z-index: 1;
        margin: 53px auto 0 auto;
        color: #fff;
        &:after {
            content: url(../img/section-7-btn.svg);
            display: block;
            position: absolute;
            top: -4px;
            left: -4px;
            z-index: -1;
        }
    }
    .section-9-mob {
        position: absolute;
        display: flex;
        flex-direction: column;
        top: 0;
        right: 60px;
        transition: all 2s ease;
        transform: translateY(-400px);
        img {
            margin-bottom: 43px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    &.scroll {
        .section-9-mob {
            transform: translateY(40px);
        }
    }
    .section-9-title {
        margin-right: 266px;
    }
    @media screen and (max-width: 1199px) {
        h2 {
            font-size: 45px;
            line-height: 50px;
        }
        .section-9__btn {
            zoom: 90%;
        }
        .section-9-mob {
            right: 30px;
        }
    }
    @media screen and (max-width: 767px) {
        h2 {
            font-size: 40px;
            line-height: 49px;
        }
        .section-9__btn {
            zoom: 80%;
        }
        .section-9-title {
            margin-right: 190px;
            transform: translateX(-20px);
        }
        
        .section-9-mob {
            right: 10px;
            .section-9-mob_1 {
                zoom: 90%;
            }
            .section-9-mob_2 {
                zoom: 90%;
            }
        }
    }
    @media screen and (max-width: 575px) {
        .section-9-title {
            margin-right: 0px;
            transform: translateX(0);
            zoom: 100%;
        }
        .section-9-mob {
            display: none;
        }
        .section-9__btn {
            font-size: 20px;
            line-height: 24px;
            zoom: 100%;
            margin-top: 39px;
            zoom: 110%;
            width: 172px;
                height: 51px;
            &:after {
                content: url(../img/section-9-btn-mob.png);
                width: 172px;
                height: 51px;
                left: calc(50% - 86px);
                top: calc(50% - 28.5px);
            }
        }

    }
    @media screen and (max-width: 360px) {
        padding: 240px 0;
        h2 {
            font-size: 35px;
            line-height: 43px;
        }
        .section-9__btn {
            zoom: 100%;
        }
    }
}