.section-6 {
    background: #0F0F0F url(../img/section-6-bg.jpg) no-repeat center center/cover;
    max-width: 100vw;
    overflow: hidden;
    padding-top: 212px;
    padding-bottom: 236px;
    position: relative;
    max-height: 570px;
    h2 {
        position: relative;
        font-weight: bold;
        font-size: 60px;
        line-height: 73px;
        text-align: center;
        height: 73px;
        span {
            color: #fff;
            position: absolute;
            width: 100%;
            top: 0;
            left: 0;
            &:last-child {
                display: none;
            }
        }
    }
    .title-desc {
        font-size: 30px;
        line-height: 40px;
        color: $grey;
        text-align: center;
        font-weight: bold;
    }
    .scroll-free {
        position: absolute;
        display: flex;
        flex-direction: column;
        transition: all 2s ease;
        img {
            margin-bottom: 25px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    .scroll-free_1 {
        top: 0px;
        left: 0;
        transform: translate(-390px, -270px);
        
    }
    .scroll-free_2 {
        top: 0px;
        right: 0;
        transform: translate(390px, -270px);
    }
    &.scroll {

        .scroll-free_1 {
            transform: translate(-390px, 0);
        }
        .scroll-free_2 {
            transform: translate(390px, 0);
        }
    }


    @media screen and (max-width: 1350px) {
        h2 {
            
            span {
                white-space: normal;
                padding: 0 100px;
                top: -80px;
            }
        }
    }
    @media screen and (max-width: 991px) {
        h2 {
            
            span {
                font-size: 45px;
                line-height: 60px;
                white-space: normal;
                padding: 0 90px;
                top: -50px;
            }
        }
        .title-desc {
            padding: 0 143px;
        }
    }
    @media screen and (max-width: 767px) {
        h2 {
            span {
                font-size: 40px;
                line-height: 43px;
            }
        }
        .title-desc {
            font-size: 25px;
            line-height: 30px;
            transform: translateY(30px);
        }
    }
    @media screen and (max-width: 575px) {
        height: 90vh;
        max-height: 800px;
        h2 {
            span {
                padding: 0 47px;
                // &:nth-child(1) {
                //     display: none;
                // }
                // &:nth-child(2) {
                //     display: inline;
                // }
            }
        }
        .title-desc {
            padding: 0 71px;
        }
        .scroll-free {
            display: none;
        }
    }
    @media screen and (max-width: 420px) {

        h2 {
            span {
                padding: 0;
            }
        }
        .title-desc {
            padding: 0 4px;
        }
    }
    @media screen and (max-width: 350px) {

        h2 {
            span {
                font-size: 35px;
                line-height: 43px;
            }
        }
        .title-desc {
            padding: 0;
            font-size: 25px;
            line-height: 30px;
        }
    }
}
.quote-nowrap {
    .quote-title {
        white-space: nowrap;
        transform: translateX(-40px);
        @media screen and (max-width: 1300px) {
            white-space: normal;
            transform: none;
        }
    }
}