* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    outline: none;
    &:focus {
        outline: none;
    }
    // border: 1px solid red;
    
    // &::-webkit-scrollbar {width: 0px;
    //     height: 0;
    // }
    
}

button {
    border: none;
    background: none;
    outline: none;
    &:focus {
        outline: none;
    }
}
a {
    text-decoration: none;
    text-transform: none;
    outline: none;
  
    &:focus {
        text-decoration: none;
        text-transform: none;
        outline: none;
    }
    &:active {
        text-decoration: none;
        text-transform: none;
        outline: none;
    }
    &:hover {
        text-decoration: none;
        text-transform: none;
        outline: none;
      
    }
}
body {
    color: #fff;
    font-size: 32px;
    line-height: 39px;
    font-family: 'Montserrat', sans-serif;
    background: #0F0F0F;
    padding: 45px 52px 0 52px;
    
    position: relative;
    span {
        color: $grey;
    }
    &:after {
        content: '';
        display: block;
        position: absolute;
        height: 100%;
        width: 11px;
        background: #F70000;
        top: 0;
        z-index: 999;
        right: 0;
    }
    &:before {
        content: '';
        display: block;
        position: absolute;
        height: 100%;
        width: 11px;
        background: #F70000;
        top: 0;
        z-index: 999;
        left: 0;
    }
}

section {
    box-shadow: 20px 20px 40px #000;
    max-width: 100vw;
}


@import "header";

@import "section-2";
@import "section-3";
@import "section-4";
@import "section-5";
@import "section-6";
@import "section-7";
@import "section-8";
@import "section-9";

@import "footer";


@media screen and (max-width: 1300px) {
    body {
        padding: 30px;
    }
}
@media screen and (max-width: 1035px) and (min-width: 992px) {
    body {
        padding-right: 0;
        padding-left: 0;
    }
}
@media screen and (max-width: 991px) {
    body {
        padding: 20px;
        &:after {
            width: 6px;
        }
        &:before {
            width: 6px;
        }
    }
}
@media screen and (max-width: 767px) {
    .quote-title {
        font-size: 30px;
        line-height: 40px;
    }
}
@media screen and (max-width: 590px) {
    body {
       padding-right: 10px;
       padding-left: 10px;
        &:after {
            display: none;
        }
        &:before {
            display: none;
        }
    }
}

@media screen and (max-width: 575px) {
    body {
       padding-right: 20px;
       padding-left: 20px;
    }
    .quote-title {
        font-size: 25px;
        line-height: 30px;
    }
    .quote img {
        zoom: .9;
    }
}
@media screen and (max-width: 370px) {
    .quote-title {
        font-size: 20px;
        line-height: 30px;
    }
    .quote img {
        zoom: .8;
    }
}