.section-7 {
    background: #0F0F0F url(../img/section-7-bg.jpg) no-repeat center center/cover;
    padding: 120px 0;
    h2 {
        font-size: 40px;
        line-height: 49px;
        font-weight: bold;
        text-align: center;
        white-space: nowrap;
        transform: translateX(-50px);
    }
    .title-desc {
        font-size: 30px;
        line-height: 37px;
        font-weight: bold;
        text-align: center;
    }
    &__btn {
        display: block;
        margin: 44px auto 0 auto;
        color: #fff;
        line-height: 70px;
        height: 70px;
        width: 295px;
        font-size: 22px;
        line-height: 27px;
        position: relative;
        z-index: 1;
        transition: all 1s ease;
        transform: translateY(100px);

        &:after {
            content: url(../img/section-7-btn.svg);
            display: block;
            position: absolute;
            top: -4px;
            left: -4px;
            z-index: -1;
        }
    }
    &.scroll {
        .section-7__btn {
            transform: translateY(0);
        }
    }

    @media screen and (max-width: 1330px) {
        h2 {
            white-space: normal;
            transform: none;
        }
        .title-desc {
            margin-top: 20px;
        }
    }
    @media screen and (max-width: 767px) {
        height: 800px;
        padding-top: 180px;
        background-position: 0 0;
    }
    @media screen and (max-width: 390px) {
        .title-desc {
            font-size: 28px;
            line-height: 35px;
        }
        .section-7__btn {
            zoom: 90%;
        }
    }
    @media screen and (max-width: 370px) {
        h2 {
            font-size: 35px;
            line-height: 43px;

        }
        .title-desc {
            font-size: 25px;
            line-height: 30px;
        }
        
    }
    @media screen and (max-width: 335px) {
        height: 700px;
        padding-top: 130px;
        h2 {
            font-size: 33px;
            line-height: 43px;

        }
        .title-desc {
            font-size: 23px;
            line-height: 30px;
        }
        .section-7__btn {
            zoom: 80%;
        }
    }
}