.section-8 {
    margin-top: 50px;
    background: #0F0F0F url(../img/section-8-bg.jpg) no-repeat center center/cover;
    background-position: -5px;
    padding-top: 74px;
    padding-bottom: 38px;
    .logo-bottom {
        display: block;
        margin: 0 auto;
    }
    .icons {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        width: 100%;
        &-item {
            width: 33.3%;
            position: relative;
            display: flex;
            align-items: center;
            margin-top: 45px;
            justify-content: center;
            // transition: all 1s ease;
            a {
                width: auto;
                display: flex;
                align-items: center;
                justify-content: flex-start;
                img {
              
                    transition: all 2s ease;
                   
                }
                div {
                    width: auto;
                    position: absolute;
                    transform: translateX(100px);
                    white-space: nowrap;
                    color: #fff;
                    font-weight: bold;
                }
            }
            
            
            
            

            &:nth-child(1) {
                a img {
                    transform: rotate(340deg);
                }
            }
            &:nth-child(2) {
                a img {
                    transform: rotate(333deg);
                }
            }
            &:nth-child(3) {
                a img {
                    transform: rotate(330deg);
                }
            }
            &:nth-child(4) {
                a img {
                    transform: rotate(330deg);
                }
            }
            &:nth-child(5) {
                a img {
                    transform: rotate(328deg);
                }
            }
            &:nth-child(6) {
                a img {
                    transform: rotate(324deg);
                }
            }
            
        }
    }
    &.rotate {
        .icons {
            &-item {
                a {
                    &:nth-child(1) {
                        img {
                            transform: rotate(0deg);
                        }
                    }
                    &:nth-child(2) {
                        img {
                            transform: rotate(0deg);
                        }
                    }
                    &:nth-child(3) {
                        img {
                            transform: rotate(360deg);
                        }
                    }
                    &:nth-child(4) {
                        img {
                            transform: rotate(0deg);
                        }
                    }
                    &:nth-child(5) {
                        img {
                            transform: rotate(360deg);
                        }
                    }
                    &:nth-child(6) {
                        img {
                            transform: rotate(360deg);
                        }
                    }
                }
                
            }
        }
    }
    h2 {
        font-size: 40px;
        line-height: 49px;
        font-weight: bold;
        text-align: center;
        margin-top: 40px;
    }
    .office {
        display: flex;
        justify-content: center;
        margin-top: 30px;
        &-item {
            margin-right: 25px;
            position: relative;
            display: flex;
            justify-content: center;
            box-shadow: 5px 5px 20px #000;
            border-radius: 10px;
            &:last-child {
                margin-right: 0;
            }
            div {
                top: 105px;
                position: absolute;
                font-size: 28px;
                line-height: 34px;
                font-weight: bold;
            }
            
            img {
                border-radius: 10px;
                width: 273px;
                height: 419px;
                object-fit: cover; 
                filter: brightness(0.3);
            }
            

            
        }
        
    }


    @media screen and (max-width: 1199px) {
        .icons {
            justify-content: space-between;
            .icons-item {
                width: 26%;
                 zoom: 92%;
            }
        }
    }
    @media screen and (max-width: 991px) {
        .icons {
            justify-content: space-between;
            .icons-item {
                width: 40%;
                
                &:nth-child(1)  {
                    order: 1;
                }
                &:nth-child(2)  {
                    order: 3;
                }
                &:nth-child(3)  {
                    order: 2;
                }
                &:nth-child(4)  {
                    order: 4;
                }
                &:nth-child(5)  {
                    order: 5; 
                }
                &:nth-child(6)  {
                    order: 6;
                }
            }
        }
        .office {
            // justify-content: space-between;
        }
        .office-item {
            zoom: 80%;
        }
    }
    @media screen and (max-width: 768px) {
        .logo-bottom {
            zoom: 75%;
        }
        .icons {
            .icons-item {
                zoom: 75%;
            }
        }
        .office {
            // justify-content: space-between;
        }
        h2 {
            font-size: 30px;
            line-height: 40px;
        }
        .office-item {
            zoom: 63%;
        }
    }
    @media screen and (max-width: 575px) {
        .office {
            flex-direction: column;
            align-items: center;
            margin-top: 0;
        }
        .icons {
            .icons-item {
                zoom: 100%;
                a {
                    img {
                        zoom: 65%;
                    }
                    div {
                        font-size: 20px;
                        transform: translateX(60px);
                    }
                }
            }
        }
        .office-item {
            margin-top: 20px;
            zoom: 100%;
            margin-right: 0;
            width: 273px;
            height: 71px;
            zoom: 140%;
            
            div {
                z-index: 999;
                font-size: 20px;
                line-height: 24px;
                height: 24px;
                top: calc(50% - 12px);
            }
            img {
                width: 273px;
                height: 71px;
                border-radius: 5px;
                filter: brightness(0.5);
            }
        }
    }
    @media screen and (max-width: 450px) {
        padding-top: 60px;
        .logo-bottom {
            zoom: 50%;
        }
        h2 {
            font-size: 25px;
            line-height: 33px;
        }
        .icons {
            margin-top: 25px;
            flex-direction: column;
            padding-left: 50px;
            transform: translateX(-22px);
            .icons-item {
                margin-top: 16px;
                width: auto;
                justify-content: flex-start;
                
                    a {
                        img {
                            zoom: 40%;
                        }
                        div {
                            font-size: 20px;
                            transform: translateX(50px);
                        }
                    }
                &:nth-child(2){
                    margin-left: -3px;
                    
                    order: 2;
                }
                &:nth-child(3){
                    margin-left: -2px;
                   
                    order: 3;
                }
                &:nth-child(4){
                    
                }
                &:nth-child(5){
                    margin-left: -5px;
                   
                }
                &:nth-child(6){
                    
                }
            }
        }
        .office-item {
            zoom: 120%;
        }
    }
    @media screen and (max-width: 400px) {
       padding-top: 70px;
        .logo-bottom {
         
        }
        h2 {
            font-size: 25px;
            line-height: 33px;
        }
        
        .office-item {
            zoom: 100%;
        }
    }
    @media screen and (max-width: 360px) {
        h2 {
            font-size: 20px;
            line-height: 24px;
        }
    }
}

