.section-4 {
    margin-top: 50px;
    padding: 73px 0 123px 0;
    background: #0F0F0F url(../img/section-4-bg.jpg) no-repeat center center/cover;

    h2 {
        font-size: 50px;
        line-height: 61px;
        font-weight: bold;
        text-align: center;
        .c-white {
            color: #fff;
        }
    }
    

    .clock {
        width: auto;
        display: flex;
        justify-content: center;
        margin-top: 36px;
        .seconds {
            display: none;
        }
        .flip-clock-divider {
            margin-left: 14px;
            margin-right: 14px;
        }
        .flip-clock-dot {
            background: #fff;
            width: 8px;
            height: 8px;
        }
        .flip {
            position: relative;
            background: none;
            width: 78px;
            height: 95px;
            margin: 0;
            margin-left: 11px;
            margin-right: 11px;
            
            &:nth-last-child(2) {
                display: none;
            }
            &:nth-last-child(1) {
                display: none;
            }
            li {
                background: none;
                .up {
                    background: none;
                    &:after {
                        display: none;
                    }
                }
                .shadow {
                    background: none;
                }
                .inn {
                    background: url(../img/date-block.png);
                    font-size: 50px;
                    line-height: 61px;
                    font-family: 'Montserrat', sans-serif;
                    font-weight: normal;
                    color: $grey;
    
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }
    
    
            
        }
        .flip-clock-divider {
            .flip-clock-label {
                display: none;
            }
            &:after {
                content: '';
                display: block;
                position: absolute;
                width: 100px;
                height: 30px;
                line-height: 30px;
                font-size: 25px;
                color: $grey;
                bottom: -44px;
                left: 87px;
                font-family: 'Montserrat', sans-serif;
                font-weight: normal;
            }
            &.days {
                &:after {
                    content: 'Дней';
                    left: 118px;
                }
            }
            &.hours {
                &:after {
                    content: 'Часов';
                }
            }
            &.minutes {
                &:after {
                    content: 'Минут';
                }
            }
        }
      }


    @media screen and (max-width: 1199px) {
        h2 {
            font-size: 45px;
            line-height: 55px;
        }
        .clock {
            zoom: 0.9;
        }
    }
    @media screen and (max-width: 991px) {
        h2 {
            padding: 0 20px;
        }
        .clock {
            zoom: 0.7;
        }
        padding: 103px 0 153px 0;
    }
    @media screen and (max-width: 767px) {
        h2 {
            padding: 0 40px;
            font-size: 42px;
            line-height: 53px;
        }
        .clock {
            zoom: 0.6;

        }
        padding: 103px 0 153px 0;
    }
    @media screen and (max-width: 575px) {
        height: 90vh;
        min-height: 724px;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        h2 {
            padding: 0 42px;
            font-size: 40px;
            line-height: 49px;
        }
        .clock {
            zoom: 1;
            margin-top: 30px;
            margin-left: 0;
            margin-right: 0;
            .flip-clock-divider {
                width: 3.5px;
                height: 44px;
                margin: 0 7px;
            }
            .flip-clock-divider.days {
                width: 0;
                margin: 0;
            }
            .flip-clock-dot {
                width: 3.5px;
                height: 3.5px;
                left: 0;
                &.top {
                    top: 31px;
                }
                &.bottom {
                    bottom: 25px;
                }
            }
            .flip {
                width: 34px;
                height: 44px;
                margin: 0 4px;
                li {
                    .inn {
                        font-size: 25px;
                        line-height: 30px;
                        background: url(../img/date-block-2.png);
                        width: 34px;
                        height: 44px;
                    }
                } 
            }
            .flip-clock-divider {
                &:after {
                    font-size: 15px;
                    line-height: 18px;
                    left: 2px;
                }
                &.days {
                    &:after {
                        left: 13px;
                    }
                }
            }
        }
    }
    @media screen and (max-width: 440px) {
        h2 {
            padding: 0px;
            font-size: 40px;
            line-height: 49px;
        }
    }
    @media screen and (max-width: 400px) {
        .clock {
            zoom: 0.98;
            .flip-clock-divider {
                margin: 0 5px;
            }
            .flip {
                margin: 0 3px;
            }
        }
    }
    @media screen and (max-width: 370px) {
        height: 90vh;
        min-height: 650px;
        h2 {
            font-size: 35px;
            line-height: 35px;
        }
        .clock {
            zoom: 0.9;
        }
    }
    @media screen and (max-width: 351px) {
        .clock {
            zoom: 0.81;
        }
    }
    @media screen and (max-width: 335px) {
        min-height: 600px;
    }
    
}


.header .gallery-block .header__header-mob {
    pointer-events: none;
}

