.header {
    background: #0F0F0F url(../img/header-bg.png) no-repeat center center/cover;
    position: relative;
    padding: 65px 0 42px 0;
    min-height: calc(100vh - 45px);
    box-shadow: 20px 20px 40px #000;

    .header__title {
        h1 {
            text-align: center;
            font-size: 35px;
            line-height: 43px;
            text-transform: uppercase;
            font-weight: bold;
            
        }
        img {
            display: block;
            margin: 29px auto 0 auto;
            
        }
    }
    
    .gallery-block {
        position: relative;
        .header__header-mob {
            position: absolute;
            display: flex;
            top: calc(50% - 275px);
            left: calc(50% - 113.5px);
            z-index: 3;
            user-select: none;
            pointer-events: none;
            // &:hover {
            //     z-index: 0;
            // }
        }
        
    }
    &__gallery {
        z-index: 1;
        margin-top: 50px;
        position: relative;
        .slick-list {
         
        }
        
        &-item {
            z-index: 1;
            transition: all 1s ease;
            
            position: relative;
            width: auto;
            img {
                box-shadow: 10px 10px 20px #000;
                width: 100%;
                height: auto;
                transform: scale(0.92);
            }
            &-text {
                font-size: 30px;
                line-height: 37px;
                position: relative;
                text-align: center;
                
                margin-top: 25px;
            }
            // img {
            //     z-index: 999;
            // }

            // &[data-slick-index='1'] {
            //     transform: scale(0.5);
            // }
        }
    }

    

    &__all {
        
        margin-top: 70px;
        text-transform: uppercase;
        font-size: 30px;
        line-height: 37px;
        text-align: center;
    }
}

.quote {
    padding-top: 35px;
    .container {
        padding: 0;
    }
    &-title {
        color: $grey;
        font-size: 35px;
        line-height: 45px;
        letter-spacing: 0px;
        text-align: center;
    }
    img {
        display: block;
        margin-left: auto;
        transform: translate(7px, -10px);
    }
}


@media screen and (max-width: 1199px) {
    .header .gallery-block .header__header-mob {
        zoom: 84%;
        top: calc(50% - 284px);
    }
}
@media screen and (max-width: 991px) {
    .header .gallery-block .header__header-mob {
        zoom: 82%;
        top: calc(50% - 285px);
    }
}
@media screen and (max-width: 767px) {
    .header__gallery-item img {
        transform: scale(1);
    }
    .header .gallery-block .header__header-mob {
        zoom: 85%;
        top: calc(50% - 229px);
    }
    .header__gallery-item {
        div {
            display: none;
        }
        img {
            box-shadow: none;
        }
    }

    
}
@media screen and (max-width: 575px) {
    .gallery-block {
        margin-top: 100px;
    }
    .header__all {
        margin-top: 175px;
        font-size: 25px;
        line-height: 30px;

    }
    .header {
        padding-top: 22px;
        padding-bottom: 33px;
    }
    .header .header__title h1 {
        font-size: 30px;
        line-height: 37px;
        text-transform: none;
    }
    .header .header__title img {
        margin-top: 10px;
        width: 151px;
        
    }
    .header .gallery-block .header__header-mob {
        zoom: 100%;
        // top: calc(50% - 231px);

        top: calc(50% - 40vw);
        left: calc(50% - 19.75vw);
        width: 39.5vw;
        height: auto;
    }
    
}
@media screen and (max-width: 530px) {
    .header .gallery-block .header__header-mob {
        top: calc(50% - 39.3vw);
        left: calc(50% - 19.5vw);
        width: 39vw;
    }
}
@media screen and (max-width: 496px) {
    .header .gallery-block .header__header-mob {
        top: calc(50% - 38.8vw);
        left: calc(50% - 19.25vw);
        width: 38.5vw;
    }
}
@media screen and (max-width: 466px) {
    .gallery-block {
        margin-top: 120px;
    }
    .header .gallery-block .header__header-mob {
        top: calc(50% - 38.3vw);
        left: calc(50% - 19vw);
        width: 38vw;
    }
}
@media screen and (max-width: 437px) {
    .header .gallery-block .header__header-mob {
        top: calc(50% - 37.4vw);
        left: calc(50% - 18.4vw);
        width: 36.8vw;
    }
}
@media screen and (max-width: 403px) {
    .header .gallery-block .header__header-mob {
        top: calc(50% - 151px);
        left: calc(50% - 75px);
        width: 150px;
    }
    .header__gallery-item img {
        transform: scale(1) ;
        width: 140px;
    }
}
@media screen and (max-width: 360px) {
    .gallery-block {
        margin-top: 80px;
    }
    .header__all {
        margin-top: 135px;
    }
}
